@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

body {
  font-family: "Barlow Semi Condensed", sans-serif;
  display: flex;
  justify-content: center;
  color: #ffffff;
  background: radial-gradient(
      134.34% 134.34% at 50% 0%,
      #1f3757 0%,
      #131537 100%
    )
    no-repeat center center fixed;
}

.icon {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  height: 145px;
  width: 145px;
  border: 18px solid;
  border-radius: 50%;
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
}

.icon:hover {
  -webkit-transform: scale(110%);
          transform: scale(110%);
}

.play__section-paper {
  border: 36px solid #4664f4;
  background: url(/rock-paper-scissors-game/static/media/icon-paper.69a8c2e9.svg) no-repeat center #ececec;
}

.play__section-rock {
  border: 36px solid #db2e4d;
  background: url(/rock-paper-scissors-game/static/media/icon-rock.8ce1e109.svg) no-repeat center #ececec;
}

.play__section-lizard {
  border: 36px solid #834ee3;
  background: url(/rock-paper-scissors-game/static/media/icon-lizard.7d2e0b18.svg) no-repeat center #ececec;
}

.play__section-spock {
  border: 36px solid #3fb7cd;
  background: url(/rock-paper-scissors-game/static/media/icon-spock.b6446e8e.svg) no-repeat center #ececec;
}

.play__section-scissors {
  border: 36px solid #eb9f0e;
  background: url(/rock-paper-scissors-game/static/media/icon-scissors.bec2f9b3.svg) no-repeat center #ececec;
}

.play__section-scissors,
.play__section-spock,
.play__section-lizard,
.play__section-rock,
.play__section-paper {
  width: 300px;
  height: 300px;
  background-size: 100px;
  position: static;
}

.play__section-scissors:hover,
.play__section-spock:hover,
.play__section-lizard:hover,
.play__section-rock:hover,
.play__section-paper:hover {
  -webkit-transform: scale(100%);
          transform: scale(100%);
}

